import React, {useState} from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';

interface CounterProps {
}

let backup: any;

const analyze = (word: string) => {
  console.log(word);
  console.log(word.length);

  let currentPosition = 0;
  let searchWordLength = 2;

  let is:any = {};

  while (word[currentPosition + searchWordLength - 1]) {
    const targetWord = word.substr(currentPosition, searchWordLength);

    const matchNum = (word.match(RegExp(targetWord, 'g')) || []).length;

    if (matchNum > 1) {
      is[targetWord] = matchNum;
    }

    currentPosition++;
  }

  console.log(is);

  /*
  for (let i: number = 0; i < word.length; i++) {
    console.log( RegExp(word[i] + word[i+1]).test(word[0] + word[1]) );
  }
  */
};

let word: string = '今日はとても楽しかった。今は楽しかった。今日という日は楽しかった。楽したいな。';

analyze(word);

const Counter: React.FC<CounterProps> = props => {
  const [state, setState] = useState({
    value: backup ? backup : '',
    length: 0,
    readingPointNum: 0,
    phrasePointNum: 0,
    exclamationMarkNum: 0
  });

  const onKeyDown = (e: any) => {
    const {value} = e.currentTarget;

    const readingPointNum = (value.match(/、|，|,/g) || []).length;
    const phrasePointNum = (value.match(/。|．|\./g) || []).length;
    const exclamationMarkNum = (value.match(/！|!/g) || []).length;

    backup = value;

    setState({
      value,
      length: String(value).length,
      readingPointNum,
      phrasePointNum,
      exclamationMarkNum
    });
  };

  return (
    <div>
      <h1 className="h1">Counting Character</h1>

      <form>
        <div className="form-group">
          <textarea
            className="form-control"
            id="exampleFormControlTextarea1"
            onKeyDown={onKeyDown}
            onChange={onKeyDown}
            value={state.value}
          ></textarea>
        </div>
      </form>

			<hr />

			<table className="table table-dark table-striped">
				<thead className="thead-dark">
					<tr>
						<th scope="col" >項目</th>
						<th scope="col">値</th>
						<th scope="col">詳細</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<th>文字数</th>
						<td>{state.length}</td>
						<td>-</td>
					</tr>
					<tr>
						<th>読点の数（、）</th>
						<td>{state.readingPointNum}</td>
						<td>-</td>
					</tr>
					<tr>
						<th>句点の数（。）</th>
						<td>{state.phrasePointNum}</td>
						<td>-</td>
					</tr>
					<tr>
						<th>感嘆符の数（！）</th>
						<td>{state.exclamationMarkNum}</td>
						<td>-</td>
					</tr>
        </tbody>
      </table>

		</div>
  );
};

export default Counter;
