import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from './Home';
import Tools from './Tools/Index';
import './App.css';

const App: React.FC = () => {
  return (
    <BrowserRouter>
      <div className="App">
        <Route exact path='/' component={Home} />
        <Route path='/tools' component={Tools} />
      </div>
    </BrowserRouter>
  );
};

export default App;
