import React from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';
import * as Icon from 'react-feather';

import Home from './Home';
import AspectRatio from './AspectRatio';
import Counter from './Counter';

interface ItemProps {
  base: string;
  className?: string;
  parentProps?: any;
  data: any[];
  color?: string;
  size?: string | number;
}

const trimUrl = (base: string): string => base.charAt(base.length-1) === '/' ? base.slice(0, -1) : base;

const Item: React.FC<ItemProps> = props => {

  const lists = props.data.map((d, i) => {
    // @TODO baseURLは消したほうが確実
    const isActive = RegExp(d.link).test(props.parentProps.location.pathname);

    return (
      <li key={i} className="nav-item">
        <Link to={`${trimUrl(props.base)}/${d.link}`} className={`nav-link${isActive ? ' active disabled' : ''}`}>
          {d.icon({color: props.color || '#999', size: props.size || 16})} {d.title}
        </Link>
      </li>
    );
  });

  return (
    <ul className={props.className || ''}>
      {lists}
    </ul>
  );
};

const Tools: React.FC = (props: any) => {
  console.log(props);
  console.log(props.match.url);

  return (
    <div className="container">
      <nav className="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
        <Link to={trimUrl(props.match.url)} className="navbar-brand col-sm-3 col-md-2 mr-0" href="#">weruio <small>(Tools)</small></Link>
        <input className="form-control form-control-dark w-100" type="text" placeholder="Search *under development" aria-label="Search" disabled />
      </nav>
      <div className="container-fluid">
        <div className="row">
          <nav className="col-md-2 d-none d-md-block bg-dark text-light text-white sidebar">
            <div className="sidebar-sticky">

              <Item
                parentProps={props}
                base={props.match.url}
                className="nav flex-column"
                data={[
                  {
                    icon: Icon.File,
                    title: '文字数カウント',
                    link: 'counter'
                  }, {
                    icon: Icon.ShoppingCart,
                    title: 'Aspect Ratio',
                    link: 'aspect-ratio'
                  }, {
                    icon: Icon.Users,
                    title: 'Customers',
                    link: 'customers'
                  }, {
                    icon: Icon.BarChart2,
                    title: 'Reports',
                    link: 'reports'
                  }, {
                    icon: Icon.Layers,
                    title: 'Integrations',
                    link: 'integrations'
                  }
                ]}
              />

              <h6 className="sidebar-heading d-flex justify-content-between align-items-center px-3 mt-4 mb-1 text-muted">
                <span>Saved reports</span>
                <a className="d-flex align-items-center text-muted" href="#">
                  <Icon.PlusCircle size="16" color="gray" />
                </a>
              </h6>

              <Item
                parentProps={props}
                base={props.match.url}
                className="nav flex-column mb=2"
                data={[
                  {
                    icon: Icon.FileText,
                    title: 'Current month',
                    link: 'currentmonth'
                  }, {
                    icon: Icon.FileText,
                    title: 'Last quarter',
                    link: 'lastquarter'
                  },
                ]}
              />
              
            </div>
          </nav>

          <main role="main" className="col-md-9 ml-sm-auto col-lg-10 px-4">
            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
            </div>

            <div className="table-responsive">
                <Route path={`${props.match.url}`} component={Home} exact />
                <Route path={`${props.match.url}/counter`} component={Counter} />
                <Route path={`${props.match.url}/aspect-ratio`} component={AspectRatio} />
            </div>
          </main>
        </div>
      </div>
    </div>
  );
};

export default Tools;
