import React, {useState} from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom';

const AspectRatio: React.FC = props => {
  const [x, setX] = useState(0);
  const [y, setY] = useState(0);
  const [ratioX, setRatioX] = useState(NaN);
  const [ratioY, setRatioY] = useState(NaN);

  const changeX = (e: any) => {
    const {value} = e.currentTarget;
    const newX = Number(value);

    if (isNaN(newX) || newX < 0) return;
    setX(newX);
    calc(newX, y);
  };

  const changeY = (e: any) => {
    const {value} = e.currentTarget;
    const newY = Number(value);

    if (isNaN(newY) || newY < 0) return;
    setY(newY);
    calc(x, newY);
  };

  const calc = (x: number, y: number) => {
    const min = Math.min(x, y);

    for (let i: number = 2; i <= min; i++) {
      if (x % i === 0 && y % i === 0) {
        x /= i;
        y /= i;
        i = 1;
      }
    }

    setRatioX(x || NaN);
    setRatioY(y || NaN);
  };

  return (
    <div>
      <h1 className="h1">Aspect Ratio</h1>

      <p>{ratioX}:{ratioY}</p>

			<form>
				<div className="form-row">
					<div className="col">
            <input
              type="text"
              className="form-control"
              placeholder="X"
              onKeyDown={changeX}
              onChange={changeX}
              value={x}
            />
					</div>
					<div className="col">
            <input
              type="text"
              className="form-control"
              placeholder="Y"
              onKeyDown={changeY}
              onChange={changeY}
              value={y}
            />
					</div>
				</div>
			</form>
    </div>
  );
};

export default AspectRatio;
